<script>
 import { createEventDispatcher } from 'svelte';

 export let defaultItem = "authors";
 export let droplist = ["titles", "tags"];
 export let selectedItem = defaultItem;

 const dispatch = createEventDispatcher();

 let hidelist = true;

 function selectItem(dropel) {
     selectedItem = dropel;
     hidelist = true;
 }
 function clickAndHide() {
     dispatch("dropdownClick");
     hidelist = !hidelist;
 }
</script>

<style>.showlist {
  border-bottom-width: 4px;
  border-color: #ff0000;
  display: table;
  z-index: 60;
}

.hidelist {
  display: none;
}

.dropdown-li {
  background-color: #fff;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  white-space: nowrap;
  color: #2d3748;
}

.dropdown-li:hover {
  border-bottom-width: 2px;
  border-color: #ff0000;
  font-style: italic;
  font-weight: 700;
  padding-left: 0.5rem;
  padding-bottom: 0.25rem;
}

li:nth-child(odd) {
  background-color: #e7e7e7;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<div on:mouseleave={() => hidelist = true} class="dropdown inline-block relative">
    <button on:click={clickAndHide} class="monocle:text-sm phone:text-sm bg-motw-red text-white font-bold p-2 inline-flex items-center text-lg focus:outline-none active:text-red-200">
        <span class="mr-1">{defaultItem.toUpperCase()}</span>
        <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
            <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
        </svg>
    </button>
    <ul class:hidelist class:showlist={!hidelist} class="dropdown-menu absolute text-white pt-1 w-full monocle:text-sm phone:text-sm text-lg cursor-pointer">
        {#each droplist as dropel}
            <li class="dropdown-li"
                on:click="{() => selectItem(dropel)}">{dropel.toUpperCase()}</li>
        {/each}
    </ul>
</div>
